* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "Space Mono", monospace;
  font-size: 10px;
}

.character-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  gap: 1rem;
  padding: 2rem 0;
  background-color: #baff39;
  border-radius: 2rem;
  height: 36rem;
  width: 30rem;
  cursor: pointer;
}
.character-container h3 {
  font-size: 2rem;
  text-align: center;
}

.character-container:hover {
  box-shadow: 2px 5px 15px 2px yellow;
}

.img {
  height: 28rem;
  width: 24rem;
  border-radius: 2rem;
}